import React, { useRef, useEffect, useState } from 'react';
import { View, Text, StyleSheet, Image, TouchableOpacity, Animated, SafeAreaView, Modal, Platform, Dimensions } from 'react-native';
import { useFocusEffect } from '@react-navigation/native';
import ScrollableGradientWrapper from '../components/ScrollableGradientWrapper';
import { Linking } from 'react-native';

// Assuming ScrollableGradientWrapper is correctly imported from '../components/ScrollableGradientWrapper'

const WelcomeScreen = ({ navigation }) => {
  const fadeAnim = useRef(new Animated.Value(1)).current;
  const [showSelectionModal, setShowSelectionModal] = useState(false);
  const modalFadeAnim = useRef(new Animated.Value(0)).current;
  const [selectedButton, setSelectedButton] = useState(null);
  const [currentFeedbackIndex, setCurrentFeedbackIndex] = useState(0);
  const handleEbayStorePress = () => {Linking.openURL('https://www.ebay.com.au/str/fluidpowergroup');};
  const feedbackFadeAnim = useRef(new Animated.Value(1)).current;

  const handleStartSelection = () => {
    setShowSelectionModal(true);
    Animated.timing(modalFadeAnim, {
      toValue: 1,
      duration: 800,
      // Use native driver where possible, but be mindful of layout properties if issues arise
      useNativeDriver: Platform.OS !== 'web', // Generally safe for opacity
    }).start();
  };

  // Helper function for modal closing animation and navigation
  const animateAndNavigate = (buttonKey, screenName, params = {}) => {
    setSelectedButton(buttonKey);
    Animated.timing(modalFadeAnim, {
      toValue: 0,
      duration: 500, // Consistent duration for closing
      useNativeDriver: Platform.OS !== 'web',
    }).start(() => {
      setShowSelectionModal(false);
      setSelectedButton(null); // Reset selection *after* animation completes
      navigation.navigate(screenName, params);
    });
  };

  const handleBuildHose = () => {
    animateAndNavigate('build', 'HoseSelection', { isHoseOnly: false });
  };

  const handleHoseOnly = () => {
    animateAndNavigate('hoseOnly', 'HoseSelection', { isHoseOnly: true });
  };

  const handleOrderFitting = () => {
    animateAndNavigate('order', 'End1Fitting', {
      selectedHose: null,
      selectedHosePrice: null,
      isOrderFittingMode: true
    });
  };

  // --- New Handler for Adapters ---
  const handleOrderAdapter = () => {
    animateAndNavigate('adapter', 'AdapterShape'); // Navigate to the new screen
  };
  // --- End New Handler ---

  const handleCloseModal = () => {
    Animated.timing(modalFadeAnim, {
      toValue: 0,
      duration: 500,
      useNativeDriver: Platform.OS !== 'web',
    }).start(() => {
      setShowSelectionModal(false);
      setSelectedButton(null);
    });
  };

  const feedbackComments = [
    { id: 1, image: require('../assets/feedback1.png')},
    { id: 2, image: require('../assets/feedback2.png')},
    { id: 3, image: require('../assets/feedback3.png')},
    { id: 4, image: require('../assets/feedback4.png')},
    { id: 5, image: require('../assets/feedback5.png')},
    { id: 6, image: require('../assets/feedback6.png')},
  ];

  const fadeIn = () => {
    fadeAnim.setValue(0);
    Animated.timing(fadeAnim, {
      toValue: 1,
      duration: 800,
      useNativeDriver: Platform.OS !== 'web',
    }).start();
  };

  useFocusEffect(
    React.useCallback(() => {
      fadeIn();
      // Ensure modal is hidden and animation reset if navigating back
      setShowSelectionModal(false);
      modalFadeAnim.setValue(0);
      setSelectedButton(null);
      return () => {
        // Optional cleanup if needed when screen loses focus
      };
    }, [])
  );

  useEffect(() => {
    const interval = setInterval(() => {
      Animated.timing(feedbackFadeAnim, {
        toValue: 0,
        duration: 400,
        useNativeDriver: true,
      }).start(() => {
        setCurrentFeedbackIndex((prev) =>
          prev === feedbackComments.length - 1 ? 0 : prev + 1
        );
        Animated.timing(feedbackFadeAnim, {
          toValue: 1,
          duration: 400,
          useNativeDriver: true,
        }).start();
      });
    }, 5000);

    return () => clearInterval(interval);
  }, [feedbackComments.length]);

  const handlePhonePress = () => {
    Linking.openURL('tel:+61409517333');
  };

  const handleEmailPress = () => {
    Linking.openURL('mailto:info@fluidpowergroup.com.au');
  };

  return (
    <View style={styles.mainContainer}>
      {/* Ensure ScrollableGradientWrapper is correctly implemented */}
      <ScrollableGradientWrapper>
        <SafeAreaView style={styles.container}>
          <View style={styles.logoContainer}>
            <Image
              source={require('../assets/fluidpower_logo.gif')}
              style={styles.logo}
              resizeMode="contain"
            />
          </View>
          <Animated.View style={[styles.contentContainer, { opacity: fadeAnim }]}>
            <View style={styles.titleContainer}>
              <Text style={styles.title}>Hydraulic Hose Builder</Text>
            </View>

            <View style={styles.messageContainer}>
              <Text style={styles.messageText}>
                You are just a <Text style={styles.highlightText}>FEW</Text> steps away from
              </Text>
              <Text style={styles.messageText}>
                <Text style={styles.highlightText}>CREATING</Text> your <Text style={styles.highlightText}>OWN HYDRAULIC HOSE</Text>
              </Text>
            </View>

            <View style={styles.imageContainer}>
              <Image
                source={require('../assets/welcome_hose.png')}
                style={styles.hoseImage}
                resizeMode="contain"
              />
            </View>

            <View style={styles.instructionsContainer}>
              <Text style={styles.instructionsText}>
                Select the <Text style={styles.highlightText2}>Hose Size</Text>, <Text style={styles.highlightText2}>End Fittings</Text>
              </Text>
              <Text style={styles.instructionsText}>
                and <Text style={styles.highlightText2}>Cut Length</Text>.
              </Text>

              <Text style={[styles.instructionsText, styles.spacedText]}>
                We will crimp and ship it the <Text style={styles.highlightText}>SAME DAY</Text>*.
              </Text>

              <Text style={[styles.instructionsText, styles.spacedText]}>
                You're all set!
              </Text>

              <Text style={[styles.instructionsText, styles.spacedText]}>
                Let's get started.
              </Text>
            </View>

            <TouchableOpacity
              style={styles.startButton}
              onPress={handleStartSelection}
            >
              <Text style={styles.startButtonText}>START SELECTION</Text>
            </TouchableOpacity>

            {/* Feedback flyer */}
            <View style={styles.feedbackFlyerContainer}>
                <Text style={styles.feedbackTitle}>Customer Feedback</Text>
                  <View style={styles.ebayStoreRow}>
                    <Text style={styles.feedbackTitle}>from </Text>
                    <TouchableOpacity onPress={handleEbayStorePress}>
                      <Text style={[styles.feedbackTitle, styles.linkText]}>eBay Store</Text>
                    </TouchableOpacity>
                  </View>

                  <View style={styles.feedbackSlideContainer}>
                    <Animated.View
                      style={[
                        styles.feedbackSlide,
                        { opacity: feedbackFadeAnim }
                      ]}
                    >
                      <Image
                        source={feedbackComments[currentFeedbackIndex].image}
                        style={styles.feedbackImage}
                        resizeMode="contain"
                      />
                      {/* Optional: Add text if your feedback includes it */}
                      {/* <Text style={styles.feedbackText}>
                        {feedbackComments[currentFeedbackIndex].text}
                      </Text> */}
                    </Animated.View>
                  </View>

                  <Text style={styles.taglineText}>Same Service New Platform</Text>

                  <View style={styles.feedbackIndicators}>
                    {feedbackComments.map((_, index) => (
                      <View
                        key={index}
                        style={[
                          styles.indicator,
                          currentFeedbackIndex === index && styles.activeIndicator
                        ]}
                      />
                    ))}
                  </View>
                </View>

            <View style={styles.asteriskContainer}>
              <Text style={styles.asteriskText}>
                *<Text style={styles.highlightText}>CUT OFF</Text> time for{' '}
                <Text style={styles.highlightText}>SAME DAY</Text> shipping is{' '}
                <Text style={styles.highlightText}>1530</Text> hrs AEST.
              </Text>

              <Text style={styles.enquiryText}>
                For any and all enquiries:
              </Text>

              <TouchableOpacity onPress={handlePhonePress}>
                <Text style={styles.contactText}>
                  +61 409 517 333
                </Text>
              </TouchableOpacity>

              <TouchableOpacity onPress={handleEmailPress}>
                <Text style={styles.contactText}>
                  email: info@fluidpowergroup.com.au
                </Text>
              </TouchableOpacity>

              <Text style={styles.copyrightText}>
                FluidPower Group All Rights Reserved {new Date().getFullYear()}
              </Text>

              <Text style={styles.appCreditsText}>
                app by aBC
              </Text>
            </View>
          </Animated.View>
        </SafeAreaView>
      </ScrollableGradientWrapper>

      {/* --- Modal Update --- */}
      <Modal
        transparent={true}
        visible={showSelectionModal}
        onRequestClose={handleCloseModal}
        animationType="none" // Control animation manually
      >
        <View style={styles.modalOverlay}>
          <TouchableOpacity
            style={styles.overlayBackground}
            activeOpacity={1}
            onPress={handleCloseModal}
          />
          <Animated.View style={[styles.selectionContainer, { opacity: modalFadeAnim, transform: [{ scale: modalFadeAnim }] }]}>
            {/* Added scale transform for a subtle zoom effect */}
            <Text style={styles.selectionTitle}>You are here to</Text>

            <TouchableOpacity
              style={[
                styles.selectionButton,
                selectedButton === 'hoseOnly' && styles.selectedButton
              ]}
              onPress={handleHoseOnly}
            >
              <Text style={styles.selectionButtonText}>Order Hose Only</Text>
            </TouchableOpacity>

            <TouchableOpacity
              style={[
                styles.selectionButton,
                selectedButton === 'order' && styles.selectedButton
              ]}
              onPress={handleOrderFitting}
            >
              <Text style={styles.selectionButtonText}>Order Fitting Only</Text>
            </TouchableOpacity>

            {/* --- New Button --- 
            <TouchableOpacity
              style={[
                styles.selectionButton,
                selectedButton === 'adapter' && styles.selectedButton // Use 'adapter' key
              ]}
              onPress={handleOrderAdapter} // Use the new handler
            >
              <Text style={styles.selectionButtonText}>Order Adapters</Text>
            </TouchableOpacity>
            {/* --- End New Button --- */}


            <TouchableOpacity
              style={[
                styles.selectionButton,
                selectedButton === 'build' && styles.selectedButton
              ]}
              onPress={handleBuildHose}
            >
              <Text style={styles.selectionButtonText}>Build Complete Hose</Text>
            </TouchableOpacity>

          </Animated.View>
        </View>
      </Modal>
      {/* --- End Modal Update --- */}
    </View>
  );
};

